#forexRateDashboardContainer{
  #sortHeader{
    display: flex;
    #sortControlsList{
      display: flex;
      flex-wrap: wrap;
      width: calc(100% - 150px);
      padding: 15px;
      button{
        margin: 5px;
        background-color: #E9F2FB;
      }
      .upArrow{
        margin-top: -10px;
      }
      .downArrow{
        margin: 0 0px -15px -24px;
      }
      .unselected{
        color: #B5B3B6;
      }
    }
    #sortClearContainer{
      padding: 15px;
      width: 150px;
      button{
        margin: 5px;
        background-color: #E9F2FB;
      }
    }
  }
  #forexRateCalculatorList{
    display: flex;
    flex-wrap: wrap;
  }
  #fabContainer{
    position: fixed;
    bottom: 25px;
    right: 25px;
  }
}