.forexRateCalculatorContainer{
  margin: 15px;
  padding: 15px;
  width: 350px;
  height: 200px;
  border: 2px solid #d5d5d5;
  border-radius: 15px;
  display: flex;
  .currencyPairLabels{
    width: 105px;
    margin-top: 15px;
    position: relative;
    .label{
      color: #0A930C;
      font-size: 1.2em;
      font-weight: 600;
    }
    svg{
      height: 100px;
      margin: 10px 0;
    }
    .rate{
      width: 85px;
      position: absolute;
      top: 72px;
      left: 30px;
      color: #686868;
      font-size: 1.0em;
      font-weight: 600;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }
  .loaderContainer{
    width: 245px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .currencyPairActions{
    width: 245px;
    .topActions{
      display: flex;
      justify-content: flex-end;
      .spacer{
        width: 15px;
      }
    }
    .textboxes{
      margin-top: 25px;
      input{
        background-color: #CEFDE4;
        color: #88A921;
        font-weight: 600;
      }
      fieldset{
        border: 2px solid #8F9592;
      }
      .spacer{
        height: 15px;
      }
    }
    .lastUpdatedDetails{
      margin-top: 15px;
      text-align: right;
      .label{
        color: #767676;
        font-weight: 600;
      }
      .value{
        color: #848484;
        font-weight: 500;
      }
    }
  }
}